import classNames from 'classnames';
import { ReactNode, FC, MouseEventHandler } from 'react';

interface Props {
	onClick: MouseEventHandler<HTMLButtonElement>;
	className?: string;
	children: ReactNode;
	disabled?: boolean;
}

const Button: FC<Props> = ({ children, onClick, className, disabled = false }) => {
	return (
		<button
			type="button"
			onClick={onClick}
			disabled={disabled}
			className={classNames('btn', { popup: !disabled }, className)}>
			{children}
		</button>
	);
};

export default Button;
