import classNames from 'classnames';
import { FC } from 'react';
import './border.css';

interface Props {
	parentClass: string;
	className?: string;
}

const FancyBorder: FC<Props> = ({ children, parentClass, className }) => {
	return (
		<div className={`fancy-border ${parentClass} ${className}`}>
			<div className={`fancy-border__background ${parentClass}__background`}>
				{children}
				</div>
		</div>
	);
};

export default FancyBorder;
