import { FC } from 'react';
import { CartItem } from '../../types';
import './cart.css';
import FancyBorder from '../common/fancy-border/border';

interface Props {
	product: CartItem;
}

const CartCard: FC<Props> = ({ product }) => {
	const {
		name,
		media: { source },
		price: { formatted },
		quantity
	} = product;
	return (
		// <div className="cart-card border-gradient">
		<FancyBorder parentClass="cart-card">
			<h2 className="cart-card__title">{name}</h2>
			<article className="cart-card__content flex">
				<div className="cart-card__left flex cart-card__flex">
					<div className="flex flex--vertical cart-card__flex cart-card__quantity-widget">
						{/* <div className="cart-card__arrow">︿</div> */}
						<FancyBorder parentClass="arrow" className="cart-card__arrow">
							︿
						</FancyBorder>
						<div className="cart-card__quantity">{`x${quantity}`}</div>
						<FancyBorder parentClass="arrow" className="cart-card__arrow">
							﹀
						</FancyBorder>
						{/* <div className="cart-card__arrow">﹀</div> */}
					</div>
					<img src={source} width="62" height="65" alt={name} className="cart-card__img" />
				</div>
				<div className="cart-card__right flex cart-card__flex">
					<div className="cart-card__price">{`${formatted}zł`}</div>
					<div className="cart-card__cancel">×</div>
				</div>
			</article>
		</FancyBorder>
		// </div>
	);
};

export default CartCard;
